import React from "react";
import { Route, Switch, Redirect, withRouter } from "react-router-dom";
import { routes } from '../consts'
import About from "./About/index";
import FormContainer from "../containers/FormContainer";
import NotFound from "./NotFound/index";
import LoginRedux from "../containers/LoginRedux";
import LoginSuccess from "./LoginSuccess/index";
import ParentContainer from "../containers/ParentContainer";

const AppRouter = props => {
    return (
        <>
            {console.log(props)}
            <Switch>
                <Route exact path={process.env.PUBLIC_URL + "/"} />
                <Route path={process.env.PUBLIC_URL + '/about'}/* {routes.about} */ component={About} />
                <Route path={routes.counters} component={ParentContainer} />
                <Route path={routes.login} component={FormContainer} />
                <Route exact path={routes.loginRedux} component={LoginRedux} />
                <Route path={routes.loginReduxSuccess} component={LoginSuccess} />
                <Route path={routes.notFound} component={NotFound} />
                <Redirect to={routes.notFound} />
            </Switch>
        </>
    )
}

export default withRouter(AppRouter);